import React, { Component } from 'react';
import Particles from 'react-particles-js';
// import Introduction from './components/Introduction';

var style = {
    position: 'absolute',
    // width: "100vw",
    // height: "100vh",
    width: "100%",
    height: "100%",
};

let particleParams = {
    "particles": {
      "number": {
        "value": 40,
        "density": {
          "enable": true,
          "value_area": 800
        }
      },
      "color": {
        "value": "#000000"
      },
      "shape": {
        "type": "circle",
        "stroke": {
          "width": 0,
          "color": "#000000"
        },
        "polygon": {
          "nb_sides": 5
        },
        "image": {
          "src": "img/github.svg",
          "width": 100,
          "height": 100
        }
      },
      "opacity": {
        "value": 0.5,
        "random": false,
        "anim": {
          "enable": false,
          "speed": 1,
          "opacity_min": 0.1,
          "sync": false
        }
      },
      "size": {
        "value": 3,
        "random": true,
        "anim": {
          "enable": false,
          "speed": 20,
          "size_min": 0.1,
          "sync": false
        }
      },
      "line_linked": {
        "enable": true,
        "distance": 150,
        "color": "#343a40",
        "opacity": 0.4,
        "width": 1
      },
      "move": {
        "enable": true,
        "speed": 6,
        "direction": "none",
        "random": false,
        "straight": false,
        "out_mode": "out",
        "bounce": false,
        "attract": {
          "enable": false,
          "rotateX": 600,
          "rotateY": 1200
        }
      }
    },
    "interactivity": {
      "detect_on": "canvas",
      "events": {
        "onhover": {
          "enable": false,
          "mode": "repulse"
        },
        "onclick": {
          "enable": true,
          "mode": "push"
        },
        "resize": true
      },
      "modes": {
        "grab": {
          "distance": 400,
          "line_linked": {
            "opacity": 1
          }
        },
        "bubble": {
          "distance": 400,
          "size": 40,
          "duration": 2,
          "opacity": 8,
          "speed": 3
        },
        "repulse": {
          "distance": 200,
          "duration": 0.4
        },
        "push": {
          "particles_nb": 4
        },
        "remove": {
          "particles_nb": 2
        }
      }
    },
    "retina_detect": true
  }

class ParticlesBackground extends Component {

    render() {
        return (
            <div style={style}>
                {/* <div className = "particles"> */}
                <Particles
                    params={
                        particleParams
                        // {
                        //     "particles": {
                        //         "number": {
                        //             "value": 100,
                        //         },
                        //         "color": {
                        //             "value": "#343a40",
                        //         },
                        //         "line_linked": {
                        //             "color": "#343a40"
                        //         },
                        //         "size": {
                        //             "value": 2.5
                        //         }
                        //     },
                        //     "interactivity": {
                        //         "events": {
                        //             "onhover": {
                        //                 "enable": true,
                        //                 "mode": "repulse"
                        //             }
                        //         }
                        //     }
                        // }
                    } />
            </div>
        )
    }
}

export default ParticlesBackground;